<template>
  <div class="contacts" v-if="layout==='list'">
    <SkeletonListContact :count="3" title="Contacts" v-if="loading && page === 1" />
    <template v-else>
      <div class="widgetContainer__body widgetContainer--scrollable"
           v-infinite-scroll="load"
           :infinite-scroll-disabled="disabledLoadMore"
           :infinite-scroll-immediate="false"
           v-if="contacts.length !== 0">
        <p class="header-3">
          {{ $t('cotacts_list_header') }}
        </p>
        <ul>
          <li
            v-for="contact in contacts"
            :key="contact.id"
            @click="viewDetails(contact)">
            <div class="initialsInfoContact initialCaps">
              <span>{{ getShortName(contact) }}</span>
            </div>
            <div class="details">
              <p class="name initialCaps">
                {{ contact.name }}
              </p>
              <p class="date initialCaps">
                {{ contact.lastName }}
              </p>
            </div>
            <span class="icon-Arrow-small-right icon" />
          </li>
        </ul>
        <div class="infinite-loading"
             v-if="loading && page !== 1">
          <p>Loading...</p>
        </div>
      </div>
      <div class="noData" v-else>
        <p>
          {{ $t('no_contact') }}
        </p>
      </div>
    </template>
    <Drawer v-if="type === 'contact' && contactDetailsDialog" class="default-drawer" :components="contactDetailsRoutes" :show.sync="contactDetailsDialog" />
  </div>
  <div v-else-if="contacts.length !== 0">
    <SkeletonListContactSmall :count="6" title="Contacts" v-if="loading" />
    <div
      class="dashboard_contacts"
      v-else>
      <div class="header">
        <p class="header-1">
          {{ $t('contact_header') }}
        </p>
        <div class="go-to" @click="viewAll">
          {{ $t('dashboard_section_viewAll') }}
          <span class="icon-Arrow-small-right" />
        </div>
      </div>
      <ul class="contacts__grid">
        <li v-for="c in contacts" :key="c.id" class="contact__card" @click="viewDetails(c)">
          <span class="contact__card--short">
            {{ getShortName(c) }}
          </span>
          <span class="contact__card--full">
            {{ c.name }} {{ c.lastName }}
          </span>
        </li>
      </ul>
      <Drawer class="default-drawer" v-if="contactDetailsDialog" :components="contactDetailsRoutes" :show.sync="contactDetailsDialog" />
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';
import contactMixins from '../mixins/contact';
import Drawer from '@/components/Drawer.vue';
import SkeletonListContact from '@/components/Skeleton/SkeletonListContact.vue';
import SkeletonListContactSmall from '@/components/Skeleton/SkeletonListContactSmall.vue';

export default {
  name: 'ContactList',
  mixins: [contactMixins],
  props: {
    type: {
      type: String,
      default: 'contact'
    },
    searchterm: {
      type: String,
      default: null
    },
    initialCall: {
      type: Boolean,
      default: true
    },
    pagination: {
      type: Boolean,
      default: true
    },
    layout: {
      type: String,
      default: 'list'
    }
  },
  components: {
    Drawer,
    SkeletonListContact,
    SkeletonListContactSmall
  },
  data() {
    return {
      contactDetailsDialog: false,
      loading: true,
      page: 1,
      limit: 25,
      persistItemCount: 0,
      contactDetailsRoutes: [
        {
          name: 'details',
          component: () => import('./ContactDetails.vue'),
          default: true,
          data: {
          },
          methods: {
            fetchContacts: this.getData
          }
        },
        {
          name: 'paymentType',
          component: () => import('@m/pay/views/PaymentType.vue')
        },
        {
          name: 'intraBank',
          component: () => import('@m/pay/views/Intrabank.vue')
        },
        {
          name: 'ach',
          component: () => import('@m/pay/views/Ach.vue')
        },
        {
          name: 'check',
          component: () => import('@m/pay/views/Check.vue')
        },
        {
          name: 'domestic-wire-address',
          component: () => import('@m/pay/views/DomesticWire.vue')
        },
        {
          name: 'domestic-wire-account',
          component: () => import('@m/pay/views/DomesticWireAccount.vue')
        },
        {
          name: 'card-payment',
          component: () => import('@m/pay/views/CardPayment.vue')
        },
        {
          name: 'pay-success',
          component: () => import('@m/pay/views/PaySuccess.vue'),
          methods: {
            onPay: () => {
              this.$emit('onPay')
            }
          }
        },
        {
          name: 'edit-contact',
          component: () => import('./ContactDetailForm.vue'),
          methods: {
            fetchContacts: this.getUpdatedData
          }
        },
        {
          name: 'add-contact',
          component: () => import('./ContactDetailForm.vue'),
        },
        {
          name: 'contact-success',
          component: () => import('./ContactCreatedSuccess.vue'),
          methods: {
            fetchContacts: this.getUpdatedData,
            openContactDeatilsDrawer: this.openContactDeatilsDrawer,
          }
        }
      ],
    };
  },
  created() {
    if(this.initialCall)
      this.getData();
  },
  computed: {
    ...mapGetters('contact', ['getContacts', 'getCreateContactData']),
    contacts() {
      /** @type {import('@m/contact/store/state').contactState['contacts']} */
      const contacts = this.getContacts.data;
      if(contacts.length) {
        contacts.sort((a, b) => {
          if (a.name.toLowerCase() < b.name.toLowerCase()) {
            return -1;
          }
          if (a.name.toLowerCase() > b.name.toLowerCase()) {
            return 1;
          }
          return 0;
        });
      }
      return contacts;
    },
    disabledLoadMore() {
      return (
        this.pagination === false ||
        this.loading === true ||
        this.getContacts.total <= this.page * this.limit
      );
    }
  },
  methods: {
    ...mapActions('contact', ['listAllContacts']),
    ...mapMutations('contact', [
      'updateSelectedContactData',
      'updateCreateContactData'
    ]),
    viewDetails(contact) {
      if (this.$listeners.onContactClick) {
        this.$emit('onContactClick', contact);
        return;
      }
      this.updateSelectedContactData(contact);
      this.contactDetailsRoutes = this.contactDetailsRoutes.map((r) => {
        r.default = r.name === 'details';
        return r;
      })
      setTimeout(() => {
        this.contactDetailsDialog = true;
      }, 0)
    },
    getData() {
      this.loading = true;
      let itemlimit = this.limit;
      let offset = (this.page - 1) * this.limit;
      if(this.persistItemCount > 0) {
        itemlimit = this.persistItemCount,
        offset = 0;
      }
      this.listAllContacts({
        accountId: this.getSelectedAccount.id,
        limit: itemlimit,
        offset: offset,
        query: this.searchterm,
      })
        .catch((e) => {
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          this.loading = false;
          this.persistItemCount = 0;
        });
    },
    getUpdatedData() {
      this.persistItemCount = this.page * this.limit;
      this.getData();
    },
    openContactDeatilsDrawer() {
      this.viewDetails(this.getCreateContactData);
    },
    load() {
      if (
        this.loading === false &&
        this.getContacts.total > this.page * this.limit
      ) {
        this.page++;
        this.getData();
      }
    },
    openContactDetails() {
      this.contactDetailsRoutes = this.contactDetailsRoutes.map((r) => {
        r.default = r.name === 'add-contact';
        return r;
      })
      setTimeout(() => {
        this.contactDetailsDialog = true;
      }, 0)
    },
    viewAll() {
      this.$router.push('/contact/list');
    },
    getShortName(c) {
      const [f,s] = c.name.split(' ');
      let v = f.charAt(0)
      if(!s) {
        v+=f.charAt(1) || '';
      } else {
        v+= s.charAt(0);
      }

      return v;
    }
  },
  watch: {
    /**
     * selected account listener
     */
    getSelectedAccount(newVal, oldVal) {
      if(newVal && oldVal && newVal.id !== oldVal.id) {
        this.getData();
      }
    },
    searchterm(newVal, oldVal) {
      if(newVal !== oldVal) {
        this.page = 1;
        this.getData();
      }
    }
  }
};
</script>

<style lang="scss">

