  margin-top: 17px;
  flex: 1;
  display: flex;
  overflow: hidden;

  .skeleton-list-contact {
    width: 100%;
  }

  .header-3 {
    margin-bottom: 16px;
  }

  .widgetContainer__body {
    overflow: hidden;
    overflow-y: auto;
