        transition: 0.2s;
        cursor: pointer;
        border-bottom: 1px solid #eeeeee;

        &:first-of-type {
          padding-top: 0;
          margin: 0;
        }

        &:last-of-type {
          padding-bottom: 0;
          margin-bottom: 0;
          border: none;
        }

        .initialsInfoContact {
          background: #f6f7f9;
          color: var(--branding-primary);
          text-transform: uppercase;
        }

        .details {
          margin: 0 auto 0 15px;
          padding-right: 8px;

        .name {
          font-size: 15px;
          line-height: 20px;
        }

        .date {
          color: rgba(#3c3c43, 0.6);
          font-size: 12px;
          padding-top: 2px;
        }
        }

        .icon {
          color: #c7c7c9;
          font-size: 12px;
        }
      }
    }
  }
  .noData {
    width: 100%;
  }
}

.zero-contact {
  white-space: pre-wrap;
  padding-top: 40px;
  color: rgba(#000000, 0.4);
  line-height: 24px;
  font-size: 16px;
  text-align: center;
}

.contacts__grid {
    display: flex;
    overflow-x: auto;
    column-gap: 16px;
    padding-bottom: 32px;
}
.contact__card {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    background: white;
    align-items: center;
    padding: 16px 10px 10px 10px;
    width: 100px;
    min-width: 100px;
    border-radius: var(--radius);

    &--short {
      background: #F6F7F9;
      border-radius: var(--radius);
      margin-bottom: 15px;
      color: var(--branding-primary);
      font-weight: bold;
      text-transform: uppercase;
      width: 54px;
      height: 54px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &--full {
      font-size: 14px;
      line-height: 18px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      text-align: center;
    }
}
  
</style>
